import { createUseStyles } from "react-jss";
import PropTypes from "prop-types";
import { formatColor } from "../../../../utils/field-types-helper";

const useStyles = createUseStyles({
  "color-square": {
    width: "20px",
    height: "20px",
    border: "1px solid #dddddd",
    "margin-left": "10px",
  },
  "color-text-container": {
    display: "flex",
    "align-items": "center",
  },
});

function InputColorReadOnly({ value, className, id }) {
  const classes = useStyles();
  const formattedColor = formatColor(value);

  return (
    <div className={`${className} ${classes["color-text-container"]}`} id={id}>
      {formattedColor}
      {formattedColor !== null && (
        <div
          className={classes["color-square"]}
          style={{
            backgroundColor: formattedColor,
          }}
        />
      )}
    </div>
  );
}

InputColorReadOnly.propTypes = {
  value: PropTypes.string,
  className: PropTypes.string,
  id: PropTypes.string,
  memo: PropTypes.bool,
};

export default InputColorReadOnly;
