import PropTypes from "prop-types";
import {
  getComponentFromDataType,
  getMountingCallbacks,
} from "../../utils/field-types";
import { useSelector } from "react-redux";

const selectPrimaryKeyValue = (state) => state.form.primaryKeyValue;

const Input = ({
  className,
  configurationSettings,
  control,
  dataType,
  fieldOptions,
  formState,
  id,
  isDependencyField,
  isNullable,
  loadCssThirdParty,
  name,
  permittedValues,
  readOnly,
  register,
  setValue,
  skin,
  value,
  valueLabel,
}) => {
  const primaryKeyValue = useSelector(selectPrimaryKeyValue);

  const InputComponent = getComponentFromDataType({ dataType, readOnly });
  const { onMount, onUnmount } = getMountingCallbacks({
    configurationSettings,
    dataType,
    fieldName: name,
    formState,
  });

  return (
    <InputComponent
      className={className}
      control={control}
      fieldOptions={fieldOptions}
      id={id}
      isDependencyField={isDependencyField}
      isNullable={isNullable}
      loadCssThirdParty={loadCssThirdParty}
      name={name}
      onMount={onMount}
      onUnmount={onUnmount}
      permittedValues={permittedValues}
      primaryKeyValue={primaryKeyValue}
      register={register}
      setValue={setValue}
      skin={skin}
      value={value}
      valueLabel={valueLabel}
    />
  );
};

Input.propTypes = {
  className: PropTypes.string,
  control: PropTypes.object,
  dataType: PropTypes.string,
  id: PropTypes.string,
  isNullable: PropTypes.bool,
  name: PropTypes.string,
  permittedValues: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  readOnly: PropTypes.bool,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array,
    PropTypes.number,
  ]),
};

export default Input;
